import React, { useRef, useState } from "react";
// 语法高亮
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
// 主题
import { xonokai } from "react-syntax-highlighter/dist/esm/styles/prism";
import { Tooltip } from "antd";
import { FullscreenOutlined, OrderedListOutlined, CopyrightOutlined, PlayCircleOutlined, FullscreenExitOutlined } from "@ant-design/icons";
import { copyCode } from "../../utils/common";
import icon from "../../static/image/icon.svg";
import styles from "./index.module.scss";
import { useTransition } from "transition-hook";
const eval2 = (fn) => {
  var Fun = Function; // 一个变量指向Function，防止有些前端编译工具报错
  return new Fun("return " + fn)();
};
export default function SyntaxHighlight(props) {
  let [lineNumber, setLineNumber] = useState(true);
  let [onOff, setOnOff] = useState(false);
  let { stage, shouldMount } = useTransition(onOff, 300);
  const fullscreen = () => {
    setOnOff(!onOff); // 全屏切换
  };
  const runCode = () => {
    // 把字符串转为函数
    let ex = eval2("(" + props.children + ")");
    console.log(ex([12, 12], 1));
  };

  return (
    <div>
      <div className={styles.codes}>
        <div style={{ padding: "15px 15px 0 15px", display: "flex", justifyContent: "space-between" }}>
          <img src={icon} alt="" />
          <div>
            <Tooltip placement="bottom" title={lineNumber ? "隐藏行号" : "显示行号"}>
              <OrderedListOutlined
                onClick={() => {
                  setLineNumber(!lineNumber);
                }}
                className={styles.iconStyle}
              />
            </Tooltip>
            <Tooltip placement="bottom" title={"全屏"}>
              <FullscreenOutlined onClick={fullscreen} className={styles.iconStyle} />
            </Tooltip>
            <Tooltip placement="bottom" title={"复制代码"}>
              <CopyrightOutlined onClick={() => copyCode(props.children)} className={styles.iconStyle} />
            </Tooltip>
            <Tooltip placement="bottom" title={"运行"}>
              <PlayCircleOutlined onClick={runCode} className={styles.iconStyle} />
            </Tooltip>
          </div>
        </div>
        <SyntaxHighlighter
          customStyle={{ border: "none" }}
          language="javascript" //你需要的类型，比如url、JavaScript等
          style={xonokai} //这个就是你代码高亮的样式，颜色类的
          showLineNumbers={lineNumber} //这个是显示不显示左侧的行数
          lineNumberStyle={{ color: "#fff", fontSize: 14 }} //这个是行数的样式
          wrapLines={false} //确定每行代码是否应该包装在父元素中
        >
          {props.children}
        </SyntaxHighlighter>
      </div>
      {shouldMount && (
        <div
          className={styles.mask}
          style={{
            transition: ".3s",
            transform: {
              from: "scale(0)",
              enter: "scale(1)",
              leave: "scale(0)",
            }[stage],
            opacity: stage === "enter" ? 1 : 0,
          }}
        >
          <div className={styles.code}>
            <div style={{ padding: "15px 15px 0 15px", display: "flex", justifyContent: "space-between" }}>
              <img src={icon} alt="" />
              <div>
                <Tooltip placement="bottom" title={lineNumber ? "隐藏行号" : "显示行号"}>
                  <OrderedListOutlined
                    onClick={() => {
                      setLineNumber(!lineNumber);
                    }}
                    className={styles.iconStyle}
                  />
                </Tooltip>
                <Tooltip placement="bottom" title={"取消全屏"}>
                  <FullscreenExitOutlined onClick={fullscreen} className={styles.iconStyle} />
                </Tooltip>
                <Tooltip placement="bottom" title={"复制代码"}>
                  <CopyrightOutlined onClick={() => copyCode(props.children)} className={styles.iconStyle} />
                </Tooltip>
                <Tooltip placement="bottom" title={"运行"}>
                  <PlayCircleOutlined onClick={runCode} className={styles.iconStyle} />
                </Tooltip>
              </div>
            </div>
            <SyntaxHighlighter
              customStyle={{ border: "none" }}
              language="javascript" //你需要的类型，比如url、JavaScript等
              style={xonokai} //这个就是你代码高亮的样式，颜色类的
              showLineNumbers={lineNumber} //这个是显示不显示左侧的行数
              lineNumberStyle={{ color: "#fff", fontSize: 14 }} //这个是行数的样式
              wrapLines={false} //确定每行代码是否应该包装在父元素中
            >
              {props.children}
            </SyntaxHighlighter>
          </div>
        </div>
      )}
    </div>
  );
}
